.by-ark-rable {
  .header-fixed {
    background: #f00;
    position: fixed;
    overflow: hidden;
    z-index: 1001;
    .fixed-left {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 12;
      background: transparent;
      * {
        background: transparent;
      }
      & th:not(.is-hidden) {
        background: #fff;
      }
    }
    .fixed-middle {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 8;
    }
    .fixed-right {
      position: absolute;
      top: 0;
      z-index: 10;
      background: transparent;
      * {
        background: transparent;
      }
      & th:not(.is-hidden) {
        background: #fff;
      }
    }
  }
  .footer-fixed {
    position: fixed;
    bottom: 0;
    z-index: 999;
    height: 10px;
    overflow-x: auto;
    overflow-y: hidden;
    &>div {
      height: 10px;
    }
  }
  .el-table__body-wrapper {
    &::-webkit-scrollbar-thumb {
      width: 0;
      height: 0;
      display: none;
      box-shadow: none;
      background: transparent;
      border-radius: 0;
    }
  }
}


